import {GetUserBaseInfo} from '@/api/modules/user.js'
import {AddScene} from '@/api/modules/scene.js'
export default {
	data() {
		return {
			data: {},
			wxInfo: uni.getStorageSync('wxInfo')
		};
	},
	created(){
		this.getData();
	},
	methods: {
		
		/**
		 * 二维码扫码
		 */
		openScanqr(){
			const _this = this;
			// 允许从相机和相册扫码
			wx.scanCode({
				success (res) {
					let postData = {
						ciphertext: res.result
					}
					AddScene(postData).then(res=>{
						if(res && res.data){
							uni.showToast({
								title: '添加成功！'
							})
						}
					}).catch(err=>{
						console.log('扫码加案出错！')
					})
				},
				fail() {
					uni.showToast({
						title: '扫码添加案件失败！',
						icon: 'none'
					})
				}
			  
			})
		},
		/**
		 * 获取信息
		 */
		async getData(){
			const _this = this;
			GetUserBaseInfo().then(res=>{
				if(res != null && res.data != null){
					_this.data = res.data;
				}
			}).catch(error=>{
				console.log('获取用户信息失败！')
			})
			
			//_this.data = await this.$api.json('userInfo');
			
		},
		
		/**
		 * 注销，退出
		 */
		logout(){
			uni.setStorageSync('access-token','')
			uni.setStorageSync('refresh-token','')
			uni.reLaunch({
				url: `/pages/login/login`,
			});
			
		}
	}
};
